<template>
  <div class="products">
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click.prevent="openModal(true)"></button>
      <!-- Modal -->
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120" class="text-right"></th>
          <th width="120" class="text-right"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in productsDate" :key="key">
          <td>
            {{ item.category }}
          </td>
          <td>
            {{ item.title }}
          </td>
          <td class="text-right">
            {{ item.origin_price | currency }}
          </td>
          <td class="text-right">
            {{ item.price | currency}}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else> </span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click.prevent="openModal(false,item)"></button>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click.prevent="deleteModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="modal-wrap">
      <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
        aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="image"></label>
                    <input type="text" class="form-control" id="image" placeholder=""
                      v-model="tempProduct.imageUrl" >
                  </div>
                  <div class="form-group">
                    <label for="customFile"> 
                      <i v-if="status.fileUploading" class="fas fa-spinner fa-spin"></i>
                    </label>
                    <input type="file" id="customFile" class="form-control" ref="files" @change="uploadfile">
                  </div>
                  <img :src="tempProduct.imageUrl" class="img-fluid" alt="">
                </div>
                <div class="col-sm-8">
                  <div class="form-group">
                    <label for="title"></label>
                    <input type="text" class="form-control" :class="{'is-invalid': errors.has('title')}" id="title" name="title" placeholder="" v-model="tempProduct.title" v-validate="'required'">
                    <span class="text-danger" v-if="errors.has('title')"></span>
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category"></label>
                      <input type="text" class="form-control" id="category" placeholder=""
                        v-model="tempProduct.category">
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input type="unit" class="form-control" id="unit" placeholder="" v-model="tempProduct.unit">
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="origin_price"></label>
                      <input type="number" class="form-control" id="origin_price" placeholder=""
                        v-model="tempProduct.origin_price">
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input type="number" class="form-control" :class="{'is-invalid': errors.has('price')}" id="price" name="price" placeholder=""
                        v-model="tempProduct.price" v-validate="'required'">
                      <span class="text-danger" v-if="errors.has('price')"></span>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-12">
                      <label for="DemoSite">Demo Site:</label>
                      <input type="text" class="form-control" id="DemoSite" placeholder=""
                        v-model="tempProduct.demoSite">
                    </div>
                  </div>
                  <div class="form-row">
                    <label for="Youtube">Youtube</label>
                    <div class="form-group input-group col-md-12">
                      <input type="text" class="form-control" id="Youtube" placeholder="Youtube"
                        v-model="tempProduct.youtube">
                      <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button"
                          @click="getYoutubeData">Youtube</button>
                      </div>
                    </div>
                  </div>

                  <hr>

                  <div class="form-group">
                    <label for="description"></label>
                    <textarea type="text" class="form-control" id="description" placeholder=""
                      v-model="tempProduct.description"></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content"></label>
                    <textarea type="text" class="form-control" id="content" placeholder=""
                      v-model="tempProduct.content"></textarea>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempProduct.is_enabled"
                        :true-value="1" :false-value="0">
                      <label class="form-check-label" for="is_enabled">
                        
                      </label>
                    </div>
                    <div class="form-check">
                      <input class="form-check-input" type="checkbox" id="is_featured" v-model="tempProduct.is_featured"
                        :true-value="1" :false-value="0">
                      <label class="form-check-label" for="is_featured">
                        
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-primary" @click.prevent="upDateProduct"></button>
            </div>
          </div>
        </div>
      </div>
      <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
        aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
               <strong class="text-danger">{{ tempProduct.title }}</strong> ()
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Pagination v-if="paginationData" :paginationData="paginationData" v-on:emitPage='getProducts'></Pagination>
  </div>
</template>

<script>
  import $ from 'jquery';
  import Pagination from '@/components/Pagination';
  import getYouTubeID from 'get-youtube-id';

  export default {
    name: 'Products',
    data() {
      return {
        productsDate: [],
        paginationData: {},
        tempProduct: {},
        isNew: false,
        status: {
          fileUploading: false
        }
      }
    },
    created() {
      this.getProducts();
      //this.$bus.$emit('message:push', '', 'success');
    },
    methods: {
      getProducts(page = 1) {
        const apiUrl = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`;
        const vm = this;

        vm.isLoading = true;

        this.$http.get(apiUrl)
          .then(res => {

            if (!res.data.success) {
              vm.$router.push('/login');
            }
            vm.productsDate = res.data.products;
            vm.paginationData = res.data.pagination;
            vm.isLoading = false;
          })
          .catch(err => {
            console.error(err);
          });
      },
      openModal(isNew, item) {
        if (isNew) {
          this.tempProduct = {};
          this.isNew = true;
        } else {
          this.tempProduct = Object.assign({}, item);
          this.isNew = false;
        }
        $('#productModal').modal('show');
      },
      deleteModal(item) {
        this.tempProduct = Object.assign({}, item);
        $('#delProductModal').modal('show');
      },
      uploadfile() {
        const upconfileFile = this.$refs.files.files[0];
        const vm = this;
        const fromData = new FormData();
        fromData.append('file-to-upload', upconfileFile);
        const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;

        vm.status.fileUploading = true;

        this.$http.post(url, fromData, {
          herders: {
            'Content-type': 'multipart/form-data'
          }
        }).then((response) => {
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
            vm.status.fileUploading = false;
          } else {
            this.$bus.$emit('message:push', response.data.message, 'danger');
          }
        });
      },
      upDateProduct() {
        const vm = this;
        let apiUrl = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
        let httpMethod = 'post';


        if (!vm.isNew) {
          apiUrl = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
          httpMethod = 'put';
        }

        this.$validator.validate().then((result) => {
            if (result) {
              this.$http[httpMethod](apiUrl, {
              data: vm.tempProduct
            })
            .then(res => {

              if (res.data.success) {
                $('#productModal').modal('hide');
              } else {
                $('#productModal').modal('hide');
              }
              this.getProducts();
            })
            .catch(err => {
              console.error(err);
            });
          } else {
            vm.$bus.$emit('message:push', '', 'danger');
          }
        });

        
      },
      deleteProduct() {
        const vm = this;
        let apiUrl = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        let httpMethod = 'delete';

        this.$http[httpMethod](apiUrl, {
            data: vm.tempProduct
          })
          .then(res => {

            if (res.data.success) {
              $('#delProductModal').modal('hide');
              vm.tempProduct = {};
            } else {
              $('#delProductModal').modal('hide');
            }
            this.getProducts();
          })
          .catch(err => {
            console.error(err);
          });
      },
      getYoutubeData() {
        const vm = this;
        if (vm.tempProduct.youtube) {

          vm.isLoading = true;

          const youtubeID = getYouTubeID(vm.tempProduct.youtube)
          const youtubeLink =
            `${process.env.YOUTUBEAPIPATH}?&id=${youtubeID}&key=${process.env.YOUTUBEKEY}&part=snippet,statistics&fields=items(id,snippet,statistics)`;

          this.$http.get(youtubeLink, {
              withCredentials: false
            })
            .then(res => {



              const youtubeData = res.data.items[0];

              vm.tempProduct.title = youtubeData.snippet.title;
              vm.tempProduct.imageUrl = youtubeData.snippet.thumbnails.standard.url;
              vm.tempProduct.content = youtubeData.snippet.description;


              vm.isLoading = false;
            })
            .catch(err => {
              console.error(err);
              vm.isLoading = false;
            });
        }
      }
    },
    components: {
      Pagination
    }
  }

</script>
